element {
    --resume-education-card-height: 300px;
}

body{
    color: white;
    background-color: #1f1f38;
}

.title {
    font-size: 24px;
    line-height: 1.08349;
    font-weight: 550;
    width: 1000px;
    letter-spacing: -.003em;
    font-family: 'Poppins', sans-serif;
    display: inline;
    font-style: normal;
    text-align: center;
    color: #4db5ff;
}


.testing-block {
    padding-top: 5rem;
    height: 100vh;
    min-height: 50rem;
    vertical-align: middle;
    place-items: center;
}

.testing-header {
    font-size: 24px;
    line-height: 1.08349;
    font-weight: 600;
    letter-spacing: -.003em;
    font-family: 'Poppins', sans-serif;
    display: inline;
    font-style: normal;
}

.testing-text {
    font-size: 18px;
    line-height: 1.08349;
    font-weight: 450;
    letter-spacing: -.003em;
    font-family: 'Poppins',sans-serif;
    display: inline;
    opacity: 75%;
}

.cardbox {
    background: transparent;
    border: 1px solid transparent;
    padding: 0.5rem;
    margin: auto;
    max-width: 100vw;
    width: 1000px;
    height: auto;
    vertical-align: middle;
    display: flex;
}

.resume-card {
    background: #2c2c6c;
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    margin: auto;
    margin-bottom: 1rem;
    max-width: 100vw;
    width: 1000px;
    height: 300px;
    vertical-align: middle;
    align-items: center;
    transition: 1s;
}

.resume-skill-card {
    background: #2c2c6c;
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    margin: auto;
    margin-bottom: 1rem;
    max-width: calc(100vw/2);
    width: 550px;
    height: auto;
    min-height: 300px;
    vertical-align: middle;
    align-items: center;
    transition: 1s;
}

.tesing-tile {
    margin: auto;
    width: 1000px;
    height: 25rem;
    vertical-align: middle;
    align-items: center;
}

.education-photo-left,
.education-photo-right {
    margin: auto;
    margin-bottom: 1rem;
    max-width: 100vw;
    width: auto;
    height: 300px;
}

.education-photo-left {
    margin-right: 2rem;
}

.education-photo-right {
    margin-left: 2rem;
}

.icon-gold {
    color: gold;
}

.resume-card:hover,
.resume-skill-card:hover {
    background: transparent;
    border-color: rgba(77, 181, 255, 0.4);
}
