* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
}

.home,
.resume,
.projects,
.contact {
  display: flex;
  height: 90vh;
  top: 0px;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

.resume {
  background-image: url("./assets/images/img-1.jpg");
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  color: #fff;
  font-size: 100px;

}

.projects {
  background-image: url("./assets/images/img-2.jpg");
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  color: #fff;
  font-size: 100px;
}

.contact {
  background-image: url("./assets/images/img-3.jpg");
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  min-width: 100%;
  min-height: 100%;
  color: #fff;
  font-size: 100px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background: url("../public/images/img-home.jpg") center center/cover
  no-repeat;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
